:root {
  --primary: rgb(5, 19, 26);
  --secondary: rgb(5, 19, 26);
  --dark: rgb(5, 19, 26);
  --light: rgb(255, 255, 255);
}

.card {
  margin: 100px auto 0;
  width: 800px;
  height: 1000px;
  -webkit-perspective: 500px;
          perspective: 500px;
  background-color: transparent;
  border: none;
}

.card__inner {
  width: 100%;
  height: 100%;
  transition: -webkit-transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  cursor: pointer;
  position: relative;
}

.card__inner:hover {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.card__face {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  overflow: hidden;
  border-radius: 16px;
  box-shadow: 0px 3px 18px 3px rgba(0, 0, 0, 0.2);
}

.card__face--front {
  background-image: linear-gradient(
    to bottom right,
    rgb(5, 19, 26),
    rgb(5, 19, 26));
  background-image: linear-gradient(
    to bottom right,
    var(--primary),
    var(--secondary)
  );

  display: flex;
  align-items: center;
  justify-content: center;
}
.card__face--front img {
  height: 30%;
}

.card__face--front h2 {
  color: #fff;
  font-size: 32px;
}

.card__face--back {
  background-color: rgb(255, 255, 255);
  background-color: var(--light);
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.card__content {
  width: 100%;
  height: 100%;
}

.card__header {
  position: relative;
  padding: 30px 30px 40px;
}

.card__header:after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: linear-gradient(
    to bottom left,
    rgb(5, 19, 26) 10%,
    rgb(5, 19, 26) 115%
  );
  background-image: linear-gradient(
    to bottom left,
    var(--primary) 10%,
    var(--secondary) 115%
  );
  z-index: -1;
  border-radius: 0px 0px 50% 0px;
}

