.team {
  padding-top: 150px;
  display: flex;
  width: 100%;
  align-items: flex-start;
  justify-content: center;
  background-color: #0093e9;
  background-image: linear-gradient(
    34deg,
    #0093e9 0%,
    #ce7778 46%,
    #dceaed 100%
  );
}

.new-team-1 {
  width: 70%;
  display: flexbox;
  flex-direction: row;
  padding-left: 60px;
  justify-content: space-evenly;
  align-items: flex-start;
}

.c-team {
  background-color: rgb(14, 23, 34);

  color: red;
  width: 80%;
  display: flex;
  justify-content: space-evenly;
  margin: 20px;
  border-radius: 20px;
}

.c-team h3 {
  padding: 20px;
  font-weight: bold;
}

.new-team {
  width: 70%;
  padding: 50px;
  display: inline;
  padding-top: 2px;
  padding-left: 60px;
  justify-content: space-evenly;
  align-items: center;
}

.profile-div-1 {
  width: 45%;
}

.profile-div {
  width: 50%;
}

.our-team {
  padding: 30px 0 40px;
  margin-bottom: 30px;
  background-color: #f7f5ec;
  text-align: center;
  overflow: hidden;
  position: relative;
  border-radius: 15px;
}

.our-team .picture {
  display: inline-block;
  height: 130px;
  width: 130px;
  margin-bottom: 50px;
  z-index: 1;
  position: relative;
}

.our-team .picture::before {
  content: "";
  width: 100%;
  height: 0;
  border-radius: 50%;
  background-color: rgb(5, 19, 26);
  position: absolute;
  bottom: 135%;
  right: 0;
  left: 0;
  opacity: 0.9;
  -webkit-transform: scale(3);
          transform: scale(3);
  transition: all 0.3s linear 0s;
}

.our-team:hover .picture::before {
  height: 100%;
}

.our-team .picture::after {
  content: "";
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: rgb(5, 19, 26);
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.our-team .picture img {
  width: 100%;
  height: auto;
  border-radius: 50%;
  -webkit-transform: scale(1);
          transform: scale(1);
  transition: all 0.9s ease 0s;
}

.our-team:hover .picture img {
  box-shadow: 0 0 0 14px #f7f5ec;
  -webkit-transform: scale(0.7);
          transform: scale(0.7);
}

.our-team .title {
  display: block;
  font-size: 15px;
  color: #4e5052;
  text-transform: capitalize;
}

.our-team .social {
  width: 100%;
  padding: 0;
  margin: 0;
  background-color: rgb(5, 19, 26);
  position: absolute;
  bottom: -100px;
  left: 0;
  transition: all 0.5s ease 0s;
}

.our-team:hover .social {
  bottom: 0;
}

.our-team .social li {
  display: inline-block;
}

.our-team .social li a {
  display: block;
  padding: 10px;
  font-size: 17px;
  color: white;
  transition: all 0.3s ease 0s;
  text-decoration: none;
}

.our-team .social li a:hover {
  color: rgb(5, 19, 26);
  background-color: #f7f5ec;
}

@media screen and (max-width: 850px) {
